<template>
  <el-dialog
    custom-class="interface-add-api-dialog"
    :title="mode === 'add' ? '' : ''"
    :visible.sync="dialogVisible"
    width="50%"
  >
    <div class="dialog-body">
      <ul class="-etl-list-group">
        <li class="list-item">
          <span class="title"></span>
          <span class="value">
            <el-input v-model="data.api" placeholder="" size="small"></el-input>
          </span>
        </li>
        <li class="list-item">
          <span class="title"></span>
          <span class="value">
            <el-input v-model="data.api_desc" placeholder="" size="small"></el-input>
          </span>
        </li>
        <li class="list-item">
          <span class="title"></span>
          <span class="value">
            <el-input v-model="data.api" placeholder="" size="small" disabled></el-input>
          </span>
        </li>
        <li class="list-item">
          <span class="title"></span>
          <span class="value">
            <el-input v-model="data.api_table_desc" placeholder="" size="small"></el-input>
          </span>
        </li>
        <li class="list-item">
          <span class="title">api</span>
          <span class="value">
            <el-radio-group v-model="data.api_data_type" size="mini">
              <el-radio-button label="csv">hive</el-radio-button>
              <el-radio-button label="avro">kudo</el-radio-button>
            </el-radio-group>
          </span>
        </li>
        <li class="list-item">
          <span class="title">api</span>
          <span class="value">
            <el-radio-group v-model="data.api_flume_template" size="mini">
              <el-radio-button label="small"></el-radio-button>
              <el-radio-button label="medium"></el-radio-button>
              <el-radio-button label="big"></el-radio-button>
            </el-radio-group>
          </span>
        </li>
        <template v-if="mode === 'edit'">
          <li class="list-item">
            <span class="title"></span>
            <span class="value">{{data.status_name}}</span>
          </li>
          <li class="list-item">
            <span class="title"></span>
            <span class="value">{{data.creater_user_info}}</span>
          </li>
          <li class="list-item">
            <span class="title"></span>
            <span class="value">{{data.updater_user_info}}</span>
          </li>
          <li class="list-item">
            <span class="title"></span>
            <span class="value">{{data.updated_time}}</span>
          </li>
        </template>
      </ul>
    </div>

    <span slot="footer" class="dialog-footer">
        <el-button @click="close()"> </el-button>
        <el-button type="primary" @click="save()"> </el-button>
      </span>
  </el-dialog>
</template>

<script>
export default {
  name: 'AddApi',
  data () {
    return {
      dialogVisible: false
    };
  },
  props: [
    'visible',
    'data',
    'mode'
  ],
  methods: {

    save () {
      const params = {
        api: this.data.api,
        api_desc: this.data.api_desc,
        api_table: this.data.api, // API
        api_table_desc: this.data.api_table_desc,
        api_data_type: this.data.api_data_type,
        api_flume_template: this.data.api_flume_template
      };
      if (this.mode === 'add') {
        params['bid'] = this.data.bid;
      } else {
        params['api_id'] = this.data.id;
      }
      const result = this.paramsValidate(params);
      if (typeof result === 'string') {
        this.$notice.warning({
          title: 'Warning',
          message: result
        });
      } else if (typeof result === 'boolean' && result) {
        this.sendRequest(params);
      }
    },
    paramsValidate (params) {
      const api = params.api && params.api.trim();
      const apiDesc = params.api_desc && params.api_desc.trim();
      const apiTable = params.api_table && params.api_table.trim();
      const apiTableDesc = params.api_table_desc && params.api_table_desc.trim();
      const apiDataType = params.api_data_type;
      const apiDataSize = params.api_flume_template;
      if (!api) {
        return '';
      } else {
        const regexp = /^[a-zA-Z]/;
        if (!regexp.test(api)) {
          return '';
        }
      }
      if (!apiDesc) return '';
      if (!apiTable) return '';
      if (!apiTableDesc) return '';
      if (!apiDataType) return 'API';
      if (!apiDataSize) return 'API';
      return true;
    },
    sendRequest (params) {
      const url = this.mode === 'add' ? 'api/add/' : 'api/update/';
      this.$http.post(url, params).then(data => {
        this.$emit('confirm');
        this.close();
      });
    },
    open () {
      this.dialogVisible = true;
    },
    close () {
      this.dialogVisible = false;
    }
  }
};
</script>

<style lang="scss" scoped>
  .interface-add-api-dialog {
    .-etl-list-group {
      display: flex;
      flex-wrap: wrap;
      .list-item {
        flex: 0 0 45%;
      }
      .title {
        flex: 0 0 100px;
      }
      .value {
      }
    }
  }
</style>



