<template>
  <AppTable
    title=""
    :tableRows="3"
    :border="true"
    :tableColumns="tableColumns"
    :tableData="myApplyList"
  ></AppTable>
</template>

<script>
export default {
  name: 'MyApplication',
  data () {
    return {
      tableColumns: [
        { label: 'id', field: 'bid' },
        { label: '', field: 'bname' },
        { label: '', field: 'business' },
        { label: '', field: 'audit_user_info' },
        { label: '', field: 'reason' },
        { label: '', field: 'create_time' },
        { label: '', field: 'audit_time' },
        { label: '', field: 'audit_remark' },
        { label: '', field: 'status_name' }
      ],
      myApplyList: []
    };
  },
  props: [],
  mounted () {
    this.getApplyList();
  },
  methods: {
    getApplyList () {
      this.$http.get('auth/my/apply/').then(data => {
        this.myApplyList = data.data;
      });
    }
  }
};
</script>

<style lang="scss" scoped>

</style>



