<template>
  <el-dialog
      custom-class="business-details-dialog"
      title=""
      :visible.sync="dialogVisible"
      width="60%"
    >
      <div class="dialog-body">
        <ul class="-etl-list-group">
          <li class="list-item">
            <span class="title"></span>
            <span class="value">
            <el-input v-model="copyData.bname" placeholder="" size="small"></el-input>
          </span>
          </li>
          <li class="list-item">
            <span class="title"></span>
            <span class="value">
            <el-input v-model="copyData.bename" placeholder="" size="small"></el-input>
          </span>
          </li>
          <li class="list-item">
            <span class="title">kafkaip</span>
            <span class="value">
            <el-input v-model="copyData.bkafka_ip" placeholder="kafkaip" size="small"></el-input>
          </span>
          </li>
          <li class="list-item">
            <span class="title">kafka</span>
            <span class="value">
            <el-input v-model="copyData.bkafka_port" placeholder="kafka" size="small"></el-input>
          </span>
          </li>
          <li class="list-item">
            <span class="title">kafka</span>
            <span class="value">
            <el-input v-model="copyData.bkafka_user" placeholder="kafka" size="small"></el-input>
          </span>
          </li>
          <li class="list-item">
            <span class="title">kafka</span>
            <span class="value">
            <el-input v-model="copyData.bkafka_pkey" placeholder="kafka" size="small" type="textarea" :autosize="{ minRows: 1, maxRows: 4}"></el-input>
          </span>
          </li>
          <li class="list-item">
            <span class="title">kafka  topic</span>
            <span class="value">
            <el-input v-model="copyData.bkafka_topic" placeholder="kafka  topic" size="small"></el-input>
          </span>
          </li>
          <li class="list-item">
            <span class="title">hiveip</span>
            <span class="value">
            <el-input v-model="copyData.bhive_ip" placeholder="hiveip" size="small"></el-input>
          </span>
          </li>
          <li class="list-item">
            <span class="title">hive</span>
            <span class="value">
            <el-input v-model="copyData.bhive_port" placeholder="hive" size="small"></el-input>
          </span>
          </li>
          <li class="list-item">
            <span class="title">hive</span>
            <span class="value">
            <el-input v-model="copyData.bhive_user" placeholder="hive" size="small"></el-input>
          </span>
          </li>
          <li class="list-item">
            <span class="title">hive</span>
            <span class="value">
            <el-input v-model="copyData.bhive_password" placeholder="hive" size="small"></el-input>
          </span>
          </li>
          <li class="list-item">
            <span class="title">hive</span>
            <span class="value">
            <el-input v-model="copyData.bhive_db_name" placeholder="hive" size="small"></el-input>
          </span>
          </li>
          <li class="list-item">
            <span class="title">impalaip</span>
            <span class="value">
            <el-input v-model="copyData.bimpala_ip" placeholder="impalaip" size="small"></el-input>
          </span>
          </li>
          <li class="list-item">
            <span class="title">impala</span>
            <span class="value">
            <el-input v-model="copyData.bimpala_port" placeholder="impala" size="small"></el-input>
          </span>
          </li>
          <li class="list-item">
            <span class="title">impala</span>
            <span class="value">
            <el-input v-model="copyData.bimpala_user" placeholder="impala" size="small"></el-input>
          </span>
          </li>
          <li class="list-item">
            <span class="title">impala</span>
            <span class="value">
            <el-input v-model="copyData.bimpala_password" placeholder="impala" size="small"></el-input>
          </span>
          </li>
          <li class="list-item">
            <span class="title">impala</span>
            <span class="value">
            <el-input v-model="copyData.bimpala_db_name" placeholder="impala" size="small"></el-input>
          </span>
          </li>
          <li class="list-item">
            <span class="title">zookeeper</span>
            <span class="value">
            <el-input v-model="copyData.bzk_host" placeholder="zookeeper" size="small"></el-input>
          </span>
          </li>
          <li class="list-item">
            <span class="title">zookeeper</span>
            <span class="value">
            <el-input v-model="copyData.bzk_path" placeholder="zookeeper" size="small" disabled></el-input>
          </span>
          </li>
          <li class="list-item">
            <span class="title">hiveflume</span>
            <span class="value">
            <el-input v-model="copyData.bflume_endpoint" placeholder="hiveflume" size="small"></el-input>
          </span>
          </li>
          <li class="list-item">
            <span class="title">kudokafaka connect</span>
            <span class="value">
            <el-input v-model="copyData.bkafka_connector_endpoint" placeholder="kudokafaka connect" size="small"></el-input>
          </span>
          </li>
          <li class="list-item">
            <span class="title">kudo master host</span>
            <span class="value">
            <el-input v-model="copyData.bkudo_master_host" placeholder="kudo master host" size="small"></el-input>
          </span>
          </li>
          <li class="list-item">
            <span class="title"></span>
            <span class="value">
                <div id="etl-person-selector"></div>
          </span>
          </li>
        </ul>
      </div>
      <span slot="footer" class="dialog-footer">
        <el-button @click="close()"> </el-button>
        <el-button type="primary" @click="save()"> </el-button>
      </span>
    </el-dialog>
</template>

<script>
export default {
  name: 'BusinessDetailsDialog',
  data () {
    return {
      copyData: {},
      dialogVisible: false
    };
  },
  props: ['data', 'mode'],
  mounted () {
  },
  methods: {
    initSelector (defaultSelect = []) {
      /* eslint-disable no-undef */
      $('#etl-person-selector').uCheck('etl-person-selector', defaultSelect, {
        callback: (selectList) => {
          this.copyData['bmanager'] = selectList.join(',');
          this.copyData['bmanager_info'] = selectList.join(',');
        }
      });
    },
    open () {
      this.dialogVisible = true;
    },
    close () {
      this.dialogVisible = false;
    },
    paramsValidate () {},
    sendRequest () {
      const url = this.mode === 'add' ? 'business/add/' : 'business/update/';
      if (this.mode === 'edit') {
        this.copyData.bid = this.copyData.id;
        delete this.copyData.id;
      }
      this.$http.post(url, this.copyData).then(res => {
        this.close();
        this.$emit('confirm');
      });
    },
    save () {
      this.sendRequest();
    }
  },
  watch: {
    dialogVisible (visible) {
      if (visible) {
        this.copyData = JSON.parse(JSON.stringify(this.data));
        this.$nextTick(() => {
          const defaultSelected = this.copyData['bmanager_workids'] ? this.copyData['bmanager_workids'].split(',') : [];
          this.initSelector(defaultSelected);
        });
      }
    }
  }
};
</script>

<style lang="scss" scoped>
  .business-details-dialog {
    .dialog-body {
      max-height: 500px;
      overflow: auto;
      .-etl-list-group {
        display: flex;
        justify-content: flex-start;
        flex-wrap: wrap;
        .list-item {
          flex: 0 0 48%;
          margin: 10px 0;
          .title {
            flex: 0 0 150px;
          }
        }
      }
    }
  }
</style>



